import { Navigate } from "react-router-dom";
import InsertBookForm from "./InsertBookForm";
import BootstraptTable from "react-bootstrap-table-next";

import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
import paginationFactory from "react-bootstrap-table2-paginator";
import "react-bootstrap-table2-paginator/dist/react-bootstrap-table2-paginator.min.css";
import "react-bootstrap-table2-filter/dist/react-bootstrap-table2-filter.min.css";
import filterFactory, { textFilter } from "react-bootstrap-table2-filter";
import cellEditFactory from "react-bootstrap-table2-editor";

function Sidebar({
  bookList,
  setBookName,
  setAuthorName,
  setAvailability,

  setUpdatedBookName,
  setUpdatedAuthorName,
  setUpdatedAvailability,
  handleInsert,
  handleUpdate,
  handleDelete,
}) {
  const loggedInUser = localStorage.getItem("authenticated");

  const afterSaveCell = (row, cellName, cellValue) => {
    setUpdatedBookName(cellValue.BookName);
    setUpdatedAuthorName(cellValue.AuthorName);
    setUpdatedAvailability(cellValue.Available);
    handleUpdate(cellValue.BookId);
  };

  const deleteRow = (cell, row, rowIndex, formatExtraData) => {
    return (
      <button
        className="btn btn-danger"
        onClick={() => handleDelete(row.BookId)}
      >
        Delete
      </button>
    );
  };

  const columns = [
    {
      dataField: "BookId",
      text: "BookId",
      sort: true,
      isKey: true,
      editable: false,
      headerStyle: () => {
        return { width: "200px", textAlign: "center" };
      },
    },

    {
      dataField: "BookName",
      text: "Book Name",
      sort: true,
      filter: textFilter(),
      headerStyle: () => {
        return { textAlign: "center" };
      },
    },

    {
      dataField: "AuthorName",
      text: "Author Name",
      filter: textFilter(),
      headerStyle: () => {
        return { textAlign: "center" };
      },
    },

    {
      dataField: "Available",
      text: "Available",
      sort: true,
      headerStyle: () => {
        return { width: "200px", textAlign: "center" };
      },
      validator: (newValue, row, column) => {
        if (isNaN(newValue)) {
          return { valid: false, message: "please enter a valid number" };
        }
        return true;
      },
    },
    {
      text: "Remove",
      formatter: deleteRow,
      editable: false,
      headerStyle: () => {
        return { width: "200px", textAlign: "center" };
      },
    },
  ];

  // const selectRow = {
  //   mode: "checkbox",
  //   clickToSelect: true,
  //   clickToEdit: true,
  // };

  const pagination = paginationFactory({
    page: 1,
    sizePerPage: 10,
    firstPageText: "<<",
    lastPageText: ">>",
    nextPageText: ">",
    prePageText: "<",
    showTotal: true,
    alwaysShowAllBtns: true,
    onPageChange: (page, sizePerPage) => {
      console.log("page", page);
      console.log("sizePerPage", sizePerPage);
    },
    onSizePerPageChange: (page, sizePerPage) => {
      console.log("page", page);
      console.log("sizePerPage", sizePerPage);
    },
  });

  if (!loggedInUser) {
    return <Navigate replace to="/" />;
  } else {
    return (
      <div className="mt-4">
        <div className="container">
          <h1 className="text-center">E-Library</h1>
          <h3 className="mt-4 text-center">Book List</h3>
          <div className="d-flex justify-content-between mb-3">
            <button
              className="btn btn-primary"
              type="button"
              data-bs-toggle="modal"
              data-bs-target="#exampleModal"
            >
              Add Book
            </button>
          </div>

          <BootstraptTable
            bootstrap4
            pagination={pagination}
            columns={columns}
            keyField="BookId"
            data={bookList}
            insertRow
            deleteRow
            hover
            search
            condensed
            // selectRow={selectRow}
            filter={filterFactory()}
            cellEdit={cellEditFactory({
              mode: "dbclick",
              blurToSave: true,
              afterSaveCell: afterSaveCell.bind(this),
            })}
          />

          {/* <table id="bookTable" className="table table-hover">
            <thead>
              <tr>
                <th className="col-1">#</th>
                <th className="col-2">Book Name</th>
                <th className="col-2">Author Name</th>
                <th className="col-1">Available</th>
                <th className="col-1">Edit/Delete</th>
              </tr>
            </thead>
            <tbody>
              {bookList.map((book, index) => {
                return book.BookId === EditId ? (
                  <tr>
                    <th>{index + 1}</th>
                    <td>
                      <input
                        type="text"
                        value={updatedBookName}
                        className="form-control"
                        onChange={(e) => setUpdatedBookName(e.target.value)}
                      />
                    </td>
                    <td>
                      <input
                        type="text"
                        value={updatedAuthorName}
                        className="form-control"
                        onChange={(e) => setUpdatedAuthorName(e.target.value)}
                      />
                    </td>
                    <td>
                      <input
                        type="number"
                        value={updatedAvailability}
                        className="form-control"
                        onChange={(e) => setUpdatedAvailability(e.target.value)}
                      />
                    </td>
                    <td>
                      <button
                        className="btn btn-success"
                        onClick={() => handleUpdate(book.BookId)}
                      >
                        Update
                      </button>
                    </td>
                  </tr>
                ) : (
                  <tr key={index}>
                    <th>{index + 1}</th>
                    <td>{book.BookName}</td>
                    <td>{book.AuthorName}</td>
                    <td>{book.Available}</td>
                    <td>
                      <button
                        type="button"
                        className="btn btn-warning me-3"
                        onClick={() => handleEdit(book.BookId)}
                      >
                        Edit
                      </button>
                      <button
                        type="button"
                        className="btn btn-danger"
                        onClick={() => handleDelete(book.BookId)}
                      >
                        Delete
                      </button>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table> */}
        </div>
        <div
          class="modal fade"
          id="exampleModal"
          tabindex="-1"
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
        >
          <div class="modal-dialog">
            <div class="modal-content">
              <div class="modal-header">
                <h5 class="modal-title" id="exampleModalLabel">
                  Add Book
                </h5>
                <button
                  type="button"
                  class="btn-close"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                ></button>
              </div>
              <div class="modal-body">
                <InsertBookForm
                  bookList={bookList}
                  setBookName={setBookName}
                  setAuthorName={setAuthorName}
                  setAvailability={setAvailability}
                  handleInsert={handleInsert}
                />
              </div>
              <div class="modal-footer">
                <button
                  type="button"
                  class="btn btn-secondary"
                  data-bs-dismiss="modal"
                >
                  Close
                </button>

                <button
                  type="button"
                  class="btn btn-primary"
                  data-bs-dismiss="modal"
                  onClick={handleInsert}
                >
                  Insert
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Sidebar;
